import React from 'react'
import s from "./Loading.module.scss"

const Loading = () => {
    return (
        
            <svg className={s.spinner} viewBox="0 0 50 50">
                <circle className={s.path} cx="25" cy="25" r="20" fill="none" stroke-width="5"></circle>
            </svg >
        
    )
}



export default Loading